import React, { Component } from 'react';
import './style.css';

export default class TaskTable extends Component {
	constructor(props) {
		super(props);
		// this.students = props.students;
		this.tasks = props.tasks;
	}

	getClassName = (task, student) => {
		switch(task.taskStatus) {
			case 'Checked' :
			return !student.tasks[task.taskName] || student.tasks[task.taskName].mark === 0 ? 'no_work' : 'checked';
			case 'Checking' : 
			return student.tasks[task.taskName] && student.tasks[task.taskName].mark > 0 ? 'checked' : task.taskStatus.toLowerCase();
			case 'In Progress' :
			return student.tasks[task.taskName] && student.tasks[task.taskName].mark > 0 ? 'checked' : 'in_progress';
			case 'ToDo' :
			return task.taskStatus.toLowerCase();
			default :
			return;
		}
	}
  render() {
    return (
			<table>
				<thead>
					<tr>
						<th className='first_col'>Tasks / Student</th>
						{this.props.students.map((student) => <th key={student.gitHubName}><a href={student.gitHubLink}>{student.gitHubName}</a></th>)}
					</tr>
				</thead>
				<tbody>
					{this.tasks.map((task) => {
					return (
					<tr key={task.taskName}>
						<td className='first_col'><a href={task.taskLink}>{task.taskName}</a></td>
						{this.props.students.map((student) => {
							// console.log(student.tasks[task.taskName].mark)
							return (
							<td key={task.taskName + student.gitHubName} className={this.getClassName(task, student)}></td>
							)
						})}
					</tr>)})}
				</tbody>
			</table>
		)
  }
}