import React, { Component } from 'react';
import axios from 'axios';
import dataFile from './data/data.json';
import Select from 'react-select';
import TaskTable from './components/Table/index';
import './style.css'

export default class App extends Component {
  constructor() {
    super();
    this.gatekeeperUrl = 'https://mentor-dashboard-app.herokuapp.com';
    this.clientId = 'd79d55c75267a0795fb8';
    this.mentorsList = Object.keys(dataFile.mentors);
    this.options = this.mentorsList.map((gitHubName) => ({value : gitHubName, label : gitHubName}));
    this.state = {
      selectedMentor: localStorage.getItem('mentor') ? {value : localStorage.getItem('mentor'), label : localStorage.getItem('mentor')} : null,
      data: null,
      isLoading: false
    }
  }
  
  async componentDidMount() {
    const isCode = window.location.href.match(/\?code=(.*)/);
    if(!isCode) return;

    const token = isCode[1];
    this.setState({isLoading: true})

    try {
      const accessToken = await axios.get(`${this.gatekeeperUrl}/authenticate/` + token)
      const userData = await axios.get('https://api.github.com/user?access_token=' + accessToken.data.token)
      this.handleUserData(userData.data)
    } catch(e) {
      console.error(e)
    }

    this.setState({isLoading: false})
  }

  handleUserData = (data) => {
    const userName = data.login.toLowerCase();
    const userAvatar = data.avatar_url;
    localStorage.setItem('mentor', userName);
    this.setState({
      userAvatar, 
      userName,
      selectedMentor: {value : userName, label : userName}
    })
  }

  handleChange = (selectedMentor) => {
    this.setState({ selectedMentor });
    localStorage.setItem('mentor', selectedMentor.value);
  }

  render() {
    const { selectedMentor, userAvatar, userName, isLoading } = this.state;

    return (
      <div className='app'>
        <header>
          <Select
            className='selector'
            value={selectedMentor}
            onChange={this.handleChange}
            options={this.options}
          />
          <div className='avatar'>
            { userAvatar && <img src={userAvatar} alt='user_avatar'/> }   
            <p>{ userName && userName}</p>                       
          </div>
          <a href={`https://github.com/login/oauth/authorize?scope=user:email&client_id=${this.clientId}`}>LogIn GitHub</a>
        </header>
        <main>
          { selectedMentor && dataFile.mentors[selectedMentor.value] &&
            <TaskTable 
              tasks={dataFile.tasks}
              students={dataFile.mentors[selectedMentor.value].students}
          />}
          { selectedMentor && !dataFile.mentors[selectedMentor.value] &&
            <h2 className='apology'>Sorry, you are not a RSS mentor yet!</h2>
          }
          { isLoading &&
            <div className='loader'><span>Loading...</span></div>
          } 
        </main>               
      </div>      
    );
  }
}